  /* FORM TYPOGRAPHY*/
  .LS link[type=button],
  .LS button[type=button],
  .LS button[type=submit],
  .LS input[type=button],
  .LS input[type=submit],
  .LS input[type=reset] {
    background-color: #56baed;
    border: none;
    color: white;
    padding: 15px 0px 15px 0px; 
    width: 45%;
   
    text-align: center;
    text-decoration: none;
    display: inline-block;
    text-transform: uppercase;
    font-size: 13px;
    box-shadow: 0 10px 30px 0 rgba(95, 186, 233, 0.4);
    border-radius: 5px 5px 5px 5px;
    margin: 5px 20px 40px 20px;
    transition: all 0.2s ease-in-out;
  }

  .LS input[type=button]:hover,
  .LS input[type=submit]:hover,
  .LS input[type=reset]:hover {
    background-color: #39ace7;
  }

  .LS input[type=button]:active,
  .LS input[type=submit]:active,
  .LS input[type=reset]:active {
    -webkit-transform: scale(0.95);
    transform: scale(0.95);
  }

  .LS input[type=text],
  .LS input[type=password],
  .LS input[type=password_val],
  .LS input[type=email],
  .LS input[type=phone],
  .LS .register {
    background-color: #f6f6f6;
    border: none;
    color: #0d0d0d;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5px;
    width: 75%;
    border: 2px solid #f6f6f6;
    outline-color: transparent;
    transition: all 0.3s ease-in-out;
    border-radius: 5px 5px 5px 5px;
  }

  .LS input[type=text]:focus,
  .LS input[type=password]:focus,
  .LS input[type=password_val]:focus,
  .LS input[type=email]:focus,
  .LS input[type=phone]:focus, 
  .LS .register:focus {
    background-color: #fff;
    border-bottom: 2px solid #5fbae9;
    /* border-color: #5fbae9; */
  }

  .LS input[type=text]:placeholder,
  .LS input[type=password]:placeholder,
  .LS input[type=password_val]:placeholder,
  .LS input[type=email]:placeholder,
  .LS input[type=phone]:placeholder, 
  .LS .register:placeholder {
    color: #cccccc;
  }



